export function createCheckoutTemplate(
  httpClient: HttpClient
): CreateCheckoutTemplateSignature {
  return (
    checkoutTemplate: NonNullablePaths<
      CheckoutTemplate,
      | `customLineItems.${number}.descriptionLines.${number}.name`
      | `customLineItems.${number}.itemType`
      | `customLineItems.${number}.productName`
      | `customLineItems.${number}.quantity`
      | `lineItems.${number}.catalogReference`
      | `lineItems.${number}.catalogReference.appId`
      | `lineItems.${number}.catalogReference.catalogItemId`
      | `lineItems.${number}.quantity`,
      6
    >
  ) =>
    universalCreateCheckoutTemplate(
      checkoutTemplate,
      // @ts-ignore
      { httpClient }
    );
}

interface CreateCheckoutTemplateSignature {
  /**
   * Creates a checkout template.
   *
   * A checkout template is used to create a new checkout that will include predefined information. For example, a single link with
   * a `checkoutTemplateId` can be shared with customers and each time the link is clicked, a new checkout page will be created
   * for that customer with certain checkout information already populated.
   *
   * The customizable features include the option to allow or to lock coupon codes or gift cards. For example, if a store owner is
   * using the checkout template to offer a flash sale to their social media followers, they may want to lock the option to apply an
   * additional coupon on top of the sale being offered. If so, they can set `customization.lockedCoupon` to `true`.
   *
   * A checkout can be created with a checkout template by calling Create Checkout From Template.
   * The site may add further customizations to the new checkout and then redirect the customer using the new checkout's `checkoutUrl`.
   * @param - Checkout template to create.
   * @returns Created checkout template.
   */
  (
    checkoutTemplate: NonNullablePaths<
      CheckoutTemplate,
      | `customLineItems.${number}.descriptionLines.${number}.name`
      | `customLineItems.${number}.itemType`
      | `customLineItems.${number}.productName`
      | `customLineItems.${number}.quantity`
      | `lineItems.${number}.catalogReference`
      | `lineItems.${number}.catalogReference.appId`
      | `lineItems.${number}.catalogReference.catalogItemId`
      | `lineItems.${number}.quantity`,
      6
    >
  ): Promise<
    NonNullablePaths<
      CheckoutTemplate,
      | `status`
      | `customization.webClient.disabledContinueShoppingButton`
      | `customization.webClient.disabledPolicyAgreementCheckbox`
      | `customization.lockedGiftCard`
      | `customization.lockedCoupon`
      | `customization.disabledManualPayment`
      | `lineItems`
      | `lineItems.${number}.quantity`
      | `lineItems.${number}.catalogReference.catalogItemId`
      | `lineItems.${number}.catalogReference.appId`
      | `lineItems.${number}.catalogOverrideFields.productName.original`
      | `lineItems.${number}.catalogOverrideFields.physicalProperties.shippable`
      | `lineItems.${number}.catalogOverrideFields.paymentOption.value`
      | `customLineItems`
      | `customLineItems.${number}.quantity`
      | `customLineItems.${number}.price`
      | `customLineItems.${number}.priceDescription.original`
      | `customLineItems.${number}.itemType.preset`
      | `customLineItems.${number}.itemType.custom`
      | `customLineItems.${number}.paymentOption`
      | `customLineItems.${number}.savePaymentMethod`
      | `customLineItems.${number}.membersOnly`
      | `customLineItems.${number}.taxableAddress.addressType`
      | `customContentReference.appId`
      | `customContentReference.componentId`
      | `externalReference.appId`,
      6
    > & {
      __applicationErrorsType?: CreateCheckoutTemplateApplicationErrors;
    }
  >;
}

